}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: #131313;
  text-decoration: none;
  display: inline-block;
}

button {
  cursor: pointer;
  font-family: inherit;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

button:hover {
  transform: scale(1.1);
}

.container {
  margin-left: auto;
  margin-right: auto;
}

