.contacts {
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;

  @media screen and (max-width: 767px) {
    top: 133%;
  }

  @media screen and (min-width: 768px) {
    top: 101%;
  }

  @media screen and (min-width: 1200px) {
    top: 118%;
  }
}

.contacts__list {
  display: flex;
  gap: 16px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (max-width: 767px) {
    flex-direction: column;
  }

  @media screen and (min-width: 1200px) {
    gap: 32px;
  }
}

.contacts__item {
  background-color: getColor(primary-light);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  padding: 40px 32px;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    box-shadow: 3px 3px 20px rgb(65 65 65 / 50%),
      -3px -3px 20px rgb(65 65 65 / 50%);
  }

  @media screen and (max-width: 767px) {
    padding: 40px 32px 24px;
  }

  @media screen and (min-width: 768px) {
    padding: 40px 24px;
    flex-basis: 224px;
  }

  @media screen and (min-width: 1200px) {
    padding: 40px;
    flex-basis: 368px;
    background-color: getColor(primary-white);
  }
}

.contacts__agency {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(14 / 12);
  letter-spacing: 0.04em;
  text-transform: uppercase;

  background: getColor(accent);
  border-radius: 4px;
  padding: 4px 22px;
  display: inline-block;
  margin-bottom: 20px;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    font-weight: 500;
    font-size: 16px;
    line-height: calc(22 / 16);
    padding: 4px 13px;
  }

  @media screen and (min-width: 1200px) {
    padding: 4px 14px;
  }
}

.contacts__agency--padding {
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding: 4px 10px;
  }
}

.contacts__city {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(22 / 12);
  letter-spacing: 0.04em;
  margin-bottom: 8px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(30 / 16);
  }
}

.contacts__address {
  position: relative;
  margin-bottom: 48px;

  &::after {
    content: '';
    display: inline-block;
    left: 0;
    bottom: -24px;
    position: absolute;
    width: 216px;
    height: 1px;
    background-color: #e1e1e1;

    @media screen and (min-width: 768px) {
      bottom: -32px;
      width: 176px;
    }

    @media screen and (min-width: 1200px) {
      width: 288px;
    }
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 64px;
  }
}

.address__link {
  font-family: inherit;
  font-weight: 500;
  font-size: 12px;
  line-height: calc(22 / 12);
  letter-spacing: 0.04em;
  color: getColor('coral');
  font-style: normal;
  flex-basis: 216px;

  &:hover,
  &:focus {
    text-decoration-line: underline;
  }

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(29 / 16);
  }
}

.address__link--width {
  @media screen and (max-width: 767px) {
    max-width: 190px;
  }

  @media screen and (min-width: 768px) {
    max-width: 160px;
  }

  @media screen and (min-width: 1200px) {
    max-width: 262px;
  }
}

.work-time__day,
.work-time__time {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(22 / 12);

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(30 / 16);
  }
}

.work-time__time {
  color: getColor('grey');
  text-transform: uppercase;
}

.work-time__list {
  position: relative;
  margin-bottom: 48px;
  display: flex;
  flex-direction: column;
  gap: 8px;

  &::after {
    content: '';
    display: inline-block;
    left: 0;
    bottom: -24px;
    position: absolute;
    width: 216px;
    height: 1px;
    background-color: #e1e1e1;

    @media screen and (min-width: 768px) {
      bottom: -32px;
      width: 176px;
    }

    @media screen and (min-width: 1200px) {
      width: 288px;
    }
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 64px;
  }
}

.communication__list {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.communication__link {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(19 / 12);

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(21 / 14);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(24 / 16);
  }
}

.contacts__item:hover .contacts__agency {
  color: #fff;
  background-color: #84a178;
  transform: scale(1.1);
}
